

'use client';

import { useState, useEffect, useCallback } from 'react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
  DialogClose,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { useToast } from '@/hooks/use-toast';
import { Plus, Loader2 } from 'lucide-react';
import { useForm, Controller } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { maintenanceTicketSchema, type MaintenanceTicket } from './schema';
import { addTicket, getLookups, getNextTicketNumber, getTenantForProperty } from './actions';
import { Combobox } from '@/components/ui/combobox';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { format } from 'date-fns';
import { Switch } from '@/components/ui/switch';
import { type Room } from '@/app/property/rooms/schema';

type TicketFormData = Omit<MaintenanceTicket, 'id'>;
const ticketFormSchema = maintenanceTicketSchema.omit({ id: true });

type Lookups = {
    properties: { value: string, label: string }[];
    units: { value: string, label: string, propertyCode: string }[];
    rooms: (Room & { value: string, label: string })[];
    tenants: { value: string, label: string }[];
}

const commonIssues = [
  { value: 'Plumbing', label: 'Plumbing' },
  { value: 'Electrical', label: 'Electrical' },
  { value: 'HVAC', label: 'HVAC' },
  { value: 'AC Not Cooling', label: 'AC Not Cooling' },
  { value: 'Split AC Repair', label: 'Split AC Repair' },
  { value: 'Washing Machine Repair', label: 'Washing Machine Repair' },
  { value: 'Water Faucet Change', label: 'Water Faucet Change' },
  { value: 'Burner Repair', label: 'Burner Repair' },
  { value: 'Wash Basin Issue', label: 'Wash Basin Issue' },
  { value: 'Water Leakage', label: 'Water Leakage' },
  { value: 'Power Failure', label: 'Power Failure' },
  { value: 'Painting', label: 'Painting' },
  { value: 'Pest Control', label: 'Pest Control' },
  { value: 'Carpentry', label: 'Carpentry' },
  { value: 'General Maintenance', label: 'General Maintenance' },
];

export function AddTicketDialog({ onTicketAdded }: { onTicketAdded: () => void }) {
  const [isOpen, setIsOpen] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const { toast } = useToast();
  const [lookups, setLookups] = useState<Lookups>({ properties: [], units: [], rooms: [], tenants: [] });
  const [isAutoCode, setIsAutoCode] = useState(true);

  const {
    register,
    handleSubmit,
    control,
    reset,
    watch,
    setValue,
    formState: { errors },
  } = useForm<TicketFormData>({
    resolver: zodResolver(ticketFormSchema),
  });

  const watchedPropertyCode = watch('propertyCode');
  const watchedUnitCode = watch('unitCode');
  const watchedRoomCode = watch('roomCode');

  const filteredUnits = lookups.units.filter(u => u.propertyCode === watchedPropertyCode);
  const filteredRooms = lookups.rooms.filter(r => r.propertyCode === watchedPropertyCode && r.unitCode === watchedUnitCode);

  const fetchTenantName = useCallback(async (property: string, unit: string, room?: string) => {
    if (property && unit) {
        const result = await getTenantForProperty(property, unit, room);
        if(result.success && result.tenantName) {
            setValue('tenantName', result.tenantName);
        } else {
            setValue('tenantName', ''); // Clear if no tenant found
        }
    }
  }, [setValue]);


  useEffect(() => {
    fetchTenantName(watchedPropertyCode, watchedUnitCode, watchedRoomCode);
  }, [watchedPropertyCode, watchedUnitCode, watchedRoomCode, fetchTenantName]);


  useEffect(() => {
      const initializeForm = async () => {
        getLookups().then(setLookups);
        const nextCode = await getNextTicketNumber();
        reset({
            ticketNo: nextCode,
            requestDate: format(new Date(), 'yyyy-MM-dd'),
            propertyCode: '',
            unitCode: '',
            roomCode: '',
            tenantName: '',
            issueType: '',
            description: '',
            priority: 'Medium',
            status: 'Open',
            assignedTo: '',
            resolutionDetails: '',
            completedDate: '',
        });
      }
      if(isOpen) {
        initializeForm();
      }
  }, [isOpen, reset]);

  const onSubmit = async (data: TicketFormData) => {
    setIsSaving(true);
    const result = await addTicket(data, isAutoCode);

    if (result.success) {
      toast({
        title: 'Ticket Created',
        description: `Successfully created new ticket "${data.ticketNo}".`,
      });
      setIsOpen(false);
      onTicketAdded(); // Refresh data on parent
    } else {
      toast({
        variant: 'destructive',
        title: 'Error',
        description: result.error,
      });
    }
    setIsSaving(false);
  };

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button>
          <Plus className="mr-2 h-4 w-4" /> Create New Ticket
        </Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-2xl">
        <form onSubmit={handleSubmit(onSubmit)}>
            <DialogHeader>
            <DialogTitle>Create New Maintenance Ticket</DialogTitle>
            <DialogDescription>
                Fill in the details below to log a new maintenance issue.
            </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4 max-h-[70vh] overflow-y-auto pr-2">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                        <Label htmlFor="ticketNo">Ticket No</Label>
                        <Input id="ticketNo" {...register('ticketNo')} disabled={isAutoCode} />
                         {errors.ticketNo && <p className="text-destructive text-xs mt-1">{errors.ticketNo.message}</p>}
                    </div>
                     <div className="flex items-end space-x-2 pb-1">
                        <Switch
                            id="auto-code-switch"
                            checked={isAutoCode}
                            onCheckedChange={setIsAutoCode}
                        />
                        <Label htmlFor="auto-code-switch">Auto-generate No.</Label>
                    </div>
                     <div className="space-y-2">
                        <Label htmlFor="requestDate">Request Date</Label>
                        <Input id="requestDate" type="date" {...register('requestDate')} />
                        {errors.requestDate && <p className="text-destructive text-xs mt-1">{errors.requestDate.message}</p>}
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div className="space-y-2">
                        <Label htmlFor="propertyCode">Property</Label>
                        <Controller
                            name="propertyCode"
                            control={control}
                            render={({ field }) => (
                            <Combobox
                                options={lookups.properties}
                                value={field.value}
                                onSelect={(value) => {
                                    field.onChange(value);
                                    setValue('unitCode', '');
                                    setValue('roomCode', '');
                                }}
                                placeholder="Select Property"
                            />
                            )}
                        />
                        {errors.propertyCode && <p className="text-destructive text-xs mt-1">{errors.propertyCode.message}</p>}
                    </div>
                     <div className="space-y-2">
                        <Label htmlFor="unitCode">Unit</Label>
                        <Controller
                            name="unitCode"
                            control={control}
                            render={({ field }) => (
                            <Combobox
                                options={filteredUnits}
                                value={field.value}
                                onSelect={(value) => {
                                    field.onChange(value);
                                    setValue('roomCode', '');
                                }}
                                placeholder="Select Unit"
                                disabled={!watchedPropertyCode}
                            />
                            )}
                        />
                        {errors.unitCode && <p className="text-destructive text-xs mt-1">{errors.unitCode.message}</p>}
                    </div>
                     <div className="space-y-2">
                        <Label htmlFor="roomCode">Room</Label>
                        <Controller
                            name="roomCode"
                            control={control}
                            render={({ field }) => (
                            <Combobox
                                options={filteredRooms}
                                value={field.value || ''}
                                onSelect={field.onChange}
                                placeholder="Select Room"
                                disabled={!watchedUnitCode}
                            />
                            )}
                        />
                    </div>
                </div>
                 <div className="space-y-2">
                    <Label htmlFor="tenantName">Tenant Name</Label>
                    <Input id="tenantName" {...register('tenantName')} />
                    {errors.tenantName && <p className="text-destructive text-xs mt-1">{errors.tenantName.message}</p>}
                </div>
                <div className="space-y-2">
                    <Label htmlFor="issueType">Issue Type</Label>
                    <Controller
                        name="issueType"
                        control={control}
                        render={({ field }) => (
                            <Combobox
                                options={commonIssues}
                                value={field.value}
                                onSelect={(value) => field.onChange(value)}
                                placeholder="Select or type an issue..."
                            />
                        )}
                    />
                    {errors.issueType && <p className="text-destructive text-xs mt-1">{errors.issueType.message}</p>}
                </div>
                <div className="space-y-2">
                    <Label htmlFor="description">Description of Issue</Label>
                    <Textarea id="description" {...register('description')} />
                    {errors.description && <p className="text-destructive text-xs mt-1">{errors.description.message}</p>}
                </div>

                 <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                        <Label htmlFor="priority">Priority</Label>
                        <Controller name="priority" control={control} render={({ field }) => (
                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                                <SelectTrigger><SelectValue/></SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="Low">Low</SelectItem>
                                    <SelectItem value="Medium">Medium</SelectItem>
                                    <SelectItem value="High">High</SelectItem>
                                    <SelectItem value="Urgent">Urgent</SelectItem>
                                </SelectContent>
                            </Select>
                        )} />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="status">Status</Label>
                        <Controller name="status" control={control} render={({ field }) => (
                             <Select onValueChange={field.onChange} defaultValue={field.value}>
                                <SelectTrigger><SelectValue/></SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="Open">Open</SelectItem>
                                    <SelectItem value="In Progress">In Progress</SelectItem>
                                    <SelectItem value="On Hold">On Hold</SelectItem>
                                    <SelectItem value="Completed">Completed</SelectItem>
                                    <SelectItem value="Cancelled">Cancelled</SelectItem>
                                </SelectContent>
                            </Select>
                        )} />
                    </div>
                </div>
            </div>
            <DialogFooter>
                <DialogClose asChild>
                    <Button type="button" variant="outline" onClick={() => reset()}>Cancel</Button>
                </DialogClose>
                <Button type="submit" disabled={isSaving}>
                    {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                    Create Ticket
                </Button>
            </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}
